@font-face {
    font-family: 'Ostrich Sans';
    src: local('Ostrich Sans'),
        url(./Fonts/OstrichSansInline-Regular.otf) format('opentype');
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
}

.bg-dark {
    background-color: #353535;
}
.button-pointer {
    cursor: pointer;
}
.bg-blue {
    background-color: #28536b;
}

.bg-lightblue {
    background-color: #7ea8be;
}

.bg-khaki {
    background-color: #bbb193;
}

.color-dark {
    color: #ffffff;
}

.color-light {
    color: #f6f0ed;
}

.color-white {
    color: #fff;
}

.color-lightblue {
    color: #7ea8be;
}

.bg-lightgrey {
    background-color: rgb(24, 24, 24);
}

.border-neon {
    outline: 0;
    font-family: 'IBM Plex Sans', sans-serif;
    background-color: #353535;
    color: whitesmoke;
    border-width: 0.3vh;
    border-style: solid;
    border-radius: 1vh;
}

.border-neon-orange {
    border-color: orange;
}

.border-neon-lime {
    border-color: #7ad623;
}

.border-neon-blue {
    border-color: #0e4fe6;
}

.border-neon-red {
    border-color: #ff2353;
}

.border-neon-violet {
    border-color: #6a5acd;
}

.border-neon-translucent {
    border-color: rgba(177, 177, 177, 0.5);
}

.border-nobottom {
    border-bottom-width: 0;
}

.input-neon {
    -webkit-appearance: none;
    appearance: none;
    width: 40vh;
    height: 25px;
    background: rgb(24, 24, 24);
    outline: none;
}
.input-neon::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    border-radius: 0.7vh;
    width: 25px;
    height: 25px;
    background: whitesmoke;
    cursor: pointer;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    overflow: hidden;
}

.container {
    display: flex;
}

.justify-start {
    justify-content: flex-start;
}

.justify-end {
    justify-content: flex-end;
}

.justify-center {
    justify-content: center;
}

.of-rows {
    flex-direction: column;
}

.row {
    display: flex;
}

.content-vcenter {
    align-items: center;
}

.content-hcenter {
    justify-content: center;
}

.width-full {
    width: 100%;
}

.height-full {
    height: 100%;
}

.width-half {
    width: 50%;
}

.padding-sm {
    padding: 0.3vw;
}

.height-sm {
    height: 5vh;
}

.text-lg {
    font-size: 2.6vh;
}

.text-md {
    font-size: 1.9vh;
}

.text-xlg {
    font-size: 3.5vh;
    margin: 0;
}

.text-nunito {
    font-family: 'IBM Plex Sans', sans-serif;
}

.semi-bold {
    font-weight: 600;
}

.d-block {
    display: block;
}

.p-absolute {
    position: absolute;
}

.margin-auto {
    margin: auto;
}

.center-absolute-h {
    left: 50%;
    transform: translate(-50%, 0);
}

.center-absolute-v {
    top: 50%;
    transform: translate(0, -50%);
}

.center-absolute-both {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.text-center {
    text-align: center;
}

.border-rounded {
    border-radius: 25%;
}

.intro-logo {
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: auto;
    height: 100%;
    width: 100%;
    z-index: 10;
    background-color: #353535;
}
.intro-1,
.intro-2 {
    display: block;
    font-size: 10vw;
    font-family: 'Ostrich Sans';
    color: rgb(255, 35, 83);
}

.intro-1 {
    animation: intro-1 2.5s ease-in-out 0s forwards 1;
    transform: translateY(-200vh);
}

.intro-2 {
    animation: intro-2 2.5s ease-in-out 0s forwards 1;
    transform: translateY(200vh);
}

@keyframes intro-1 {
    0% {
        transform: translateY(-200vh);
    }
    50% {
        transform: translateY(0);
        opacity: 1;
    }
    99% {
        opacity: 1;
    }
    100% {
        transform: translateY(0);
        opacity: 0;
    }
}

@keyframes intro-2 {
    0% {
        transform: translateY(200vh);
    }
    50% {
        transform: translateY(0);
        opacity: 1;
    }
    99% {
        opacity: 1;
    }
    100% {
        transform: translateY(0);
        opacity: 0;
    }
}
